import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-account-statement03',
  templateUrl: 'account-statement03.html',
})
export class AccountStatement03Page {
  stmntDetails;

  constructor(public navCtrl: NavController,
              public alertCtrl: AlertController,
              public navParams: NavParams) {
    this.stmntDetails = JSON.parse(navParams.get("stmntDetails"));
  }


  showStatementDetail = function (listItem) {

    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Transaction Details');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage(this.generateMessage(listItem));
    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {

      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };
  generateMessage = function (itemValue) {

    let message = "";
    Object.keys(itemValue).forEach(key => {
      let value = itemValue[key];
      message = message +
        '<ion-item style="margin-top: 16px"><b>' + key.replace("_", " ") + " : " + "</b>" + value + '<br></ion-item>';
    });
    return message;
  };

  ionViewDidLoad() {

  }

}



